import * as api from './api'   //接口api
import * as utils from '../../utils'   //公共方法
import { Loadmore } from 'mint-ui';//下拉加载上拉刷新
export default {
    data(){
        return {

            examplename: 'Loadmore',
            InitialLoading: true,//初始加载
            wrapperHeight: 0,//容器高度
            topStatus: '',//顶部下拉加载状态
            bottomStatus: '',//底部上拉加载状态
            wrapperHeight: 0,//容器高度

            allLoaded:false,//数据是否加载完毕
            iDisplayStart:1,//起始页码
            iDisplayLength:10,//每页显示条数
            haveMore: true,//是否显示更多数据
            pageList:[
            ]
            
        }
    },
    methods:{
        
        goBack(knoweleId){
                //带参数跳转
                this.$router.push({
                    path:'/risingKnowledge/knowContent',
                    query:{knoweleId:knoweleId}
                    })
        },
        goTitle(){//title标题
            if(this.$route.query.knoweleTypeId=="65"){
                document.title="资费标准" ;
                console.log(document.title)      
            }else if(this.$route.query.knoweleTypeId=="0402"){
                document.title="业务办理指南" ;
                console.log(document.title)      
            }else if(this.$route.query.knoweleTypeId=="0403"){
                document.title="服务承诺" ;
                console.log(document.title)      
            }else if(this.$route.query.knoweleTypeId=="0404"){
                document.title="电力法律法规" ;
                console.log(document.title)      
            }
        },
        handleBottomChange(status) {
            this.topStatus = status;
        },
        //下拉加载更多数据
        loadTop() {
            this.getPageList();
            this.$refs.loadmore.onTopLoaded()
            this.handleTopChange("loading");//下拉时 改变状态码
            if(this.iDisplayStart ==1){
                this.allLoaded = false;//下拉刷新时解除上拉加载的禁用
                this.getPageList()
                this.iDisplayLength = 10;//下拉刷新 数据初始化
                this.handleTopChange("loadingEnd")//数据加载完毕 修改状态码
                this.$refs.loadmore.onTopLoaded();   
            }
            
           
        },
        handleTopChange(status) {
                this.bottomStatus = status;
        },
        //上拉刷新更
        loadBottom() {   
            //this.getPageList();//获取数据  
            this.allLoaded = true;// 若数据已全部获取完毕
            this.$refs.loadmore.onBottomLoaded();
             this.handleBottomChange("loading");//上拉时 改变状态码
                this.iDisplayStart += 1;
                
                    if (this.iDisplayStart <= 3) {//最多下拉三次
                        this.iDisplayLength += 10;//上拉加载 每次数值加
                    } else {
                        this.allLoaded = true;//模拟数据加载完毕 禁用上拉加载
                    }
                    this.handleBottomChange("loadingEnd");//数据加载完毕 修改状态码
                    this.$refs.loadmore.onBottomLoaded();
               
        },
        async getPageList(){//获取接口
			let formData = {
                "iDisplayStart":1,
                "iDisplayLength":10,
                "knoweleTypeId":this.$route.query.knoweleTypeId
			}
			//console.log(api.risingKnowledge,'9接口名字')
            // console.log(utils.server)
            //const res = await utils.HTTP(`${api.home}`, formData);
            const res = await utils.HTTP(`${api.risingKnowledge}`, formData);
            console.log(res)
            
             if(res.code ==1) {
               this.pageList=res.data.aaData;
               this.iDisplayStart++;//起始页码
               //console.log(" this.iDisplayStart"+":"+this.iDisplayStart)
             }
            
        },

    },
    created(){
        this.goTitle()
        this.getPageList()
    },
    component:{
         Loadmore
        }
}
    



